* {
  box-sizing: border-box;
  color: #fdfdfd;
  margin: 0;
  padding: 0;
  font-family: inter, sans-serif;
  font-size: 14px;
}

body {
  background-color: #02141c;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  padding: 0 4%;
  display: flex;
}

.main-cont {
  color: #fff;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  width: 100%;
  display: flex;
}

.btn, main .add-input-container .add-btn {
  text-align: center;
  cursor: pointer;
  align-items: center;
  max-width: 200px;
  padding: 12px 28px;
  display: flex;
}

.btn:focus, main .add-input-container .add-btn:focus {
  outline: 1px solid #01b3ff;
}

.list-cont {
  width: 100%;
}

.delete-todo-btn {
  cursor: pointer;
}

main {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

main .add-input-container {
  filter: drop-shadow(0 5px 10px #000000c1);
  justify-content: center;
  gap: 10px;
  width: 100%;
  margin: 50px 0;
  display: flex;
}

main .add-input-container input[type="text"] {
  color: #000;
  border: none;
  width: 100%;
  padding: 0 20px;
}

main .add-input-container input[type="text"]:focus {
  outline: 2px solid #01b3ff;
}

main .add-input-container .add-btn {
  color: #fff;
  background-color: #37637651;
}

main .main-heading {
  letter-spacing: -2px;
  color: #fff;
  filter: drop-shadow(0 2px 10px #00000017);
  max-width: 400px;
  margin-top: 40px;
  font-size: 3.5rem;
  font-weight: 900;
  line-height: 1.35;
}

main .gradient-wrap-text {
  color: #0000;
  background: radial-gradient(#01b3ff, #8000ff);
  -webkit-background-clip: text;
  background-clip: text;
  font-size: 3.5rem;
}

main .heading {
  text-align: center;
  text-transform: uppercase;
  color: #fdfdfd;
  background-color: #37637651;
  padding: 8px;
  font-weight: 500;
}

main .pending-todo-container, main .completed-todo-container {
  color: #fdfdfd;
  background-color: #37637651;
  flex-direction: column;
  width: 100%;
  height: max-content;
  display: flex;
  position: relative;
}

main .pending-todo-container .todo, main .completed-todo-container .todo {
  border-bottom: .1px solid #4a51547c;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  height: 50px;
  padding: 0 20px;
  display: flex;
}

main .pending-todo-container .todo a, main .completed-todo-container .todo a {
  text-decoration: none;
}

main .pending-todo-container .todo .left-side, main .completed-todo-container .todo .left-side {
  gap: 10px;
  display: flex;
}

main .pending-todo-container .todo .left-side .todo-name, main .completed-todo-container .todo .left-side .todo-name {
  font-weight: 400;
}

main .list-cont:after {
  content: "";
  background-color: #000;
  width: 110%;
  position: absolute;
}

main .completed-todo-container {
  height: max-content;
}

main .completed-todo-container li .todo-name {
  text-decoration: line-through;
}

@media screen and (width >= 750px) {
  body {
    padding: 0 7%;
  }

  main {
    width: 100%;
  }

  main .main-heading {
    max-width: 600px;
    font-size: 5rem;
  }

  main .gradient-wrap-text {
    font-size: 5rem;
  }

  main .main-cont {
    flex-direction: row;
  }

  main .add-input-container input[type="text"] {
    width: 600px;
  }
}

